        <p>Welcome to the Odiance Corp., a Canadian corporation (&ldquo;<strong>Odiance</strong>&rdquo;, &ldquo;<strong>us</strong>&rdquo; or &ldquo;<strong>we</strong>&rdquo;) social network, streaming platform and digital performance platform consisting of Web sites, services, software applications, embeddable players, and syndicated&nbsp;<em>widgets</em>&nbsp;(the &ldquo;<strong>Odiance Service</strong>&rdquo;).</p>
